.form {
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 5px 5px 5px #000000;
  background-color: #000000;
  min-width: 10vmin;
  min-height: 8vmin;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  color: #000;
  padding: 2rem;
}

.label {
  font-weight: bold;
  text-align: center;
  display: block;
  color: #fff;
}

.input {
  text-align: center;
  display: inline-block;
  color: #000;
  font-size: large;
  border-radius: 10px;
  padding: 0.5rem;
}

.error {
  color: #ff5733;
}

.submit {
  background-color: #fff;
  color: #000;
  border-radius: 10px;
  outline: none;
  transition: background-color 0.3s linear;
  text-align: center;
  display: column;
  justify-content: center;
  margin-top: 1rem;
  padding: 0.7rem;
  font-weight: bold;
}

.submit:hover {
  font-weight: bold;
  background-color: #ff5733;
  border-color: #000;
  color: #000;
  transition: background-color 0.3s linear;
}
